import request from '@/utils/request'

export function index () {
  return request({
    url: 'pages',
    method: 'get'
  })
}

export function store (data) {
  return request({
    url: 'pages',
    method: 'post',
    data
  })
}

export function show (keyName) {
  return request({
    url: 'pages/' + keyName,
    method: 'get'
  })
}

export function update (data, keyName) {
  return request({
    url: 'pages/' + keyName,
    method: 'put',
    data
  })
}

export function destroy (keyName) {
  return request({
    url: 'pages/' + keyName,
    method: 'delete'
  })
}

export function updateContent (data, keyName) {
  return request({
    url: 'pages/' + keyName + '/content/editor',
    method: 'put',
    data
  })
}

export function validateKeyName (data) {
  return request({
    url: 'pages/validate/keyname',
    method: 'post',
    data
  })
}
