import Vue from 'vue'
import './plugins/vuex'
import './plugins/pinia'
import './plugins/vue-router'
import './plugins/element'
import './plugins/vue-js-modal'
import './plugins/fontawesome'
import './plugins/tinymce'
import './plugins/vee-validate'
import './plugins/imageEditor'
import './plugins/ui-elements'
import './plugins/highcharts'
import './plugins/moments'
import './plugins/numeral'
import './plugins/codemirror'

// Global Mixin
import './mixin'
// Style Global
import '@/scss/index.scss' // global css

import App from './App'
import store from './store'
import router from './router/index'
import { createPinia } from 'pinia'

import '@/icons' // permission control
import '@/permission' // icon

import { useStoreConfig } from './stores/configStore'
import { useStoreSettings } from './stores/settingsStore'

export const pinia = createPinia()

Vue.config.productionTip = false

new Vue({
  router,
  store,
  pinia,
  render: h => h(App)
}).$mount('#app')
const configStore = useStoreConfig()
const settingsStore = useStoreSettings()
if (configStore.baseApi == null) configStore.loadConfig()
settingsStore.fetchSettings()
