import { defineStore } from 'pinia'
// import { Message } from 'element-ui'

const langKey = 'langKeyManagerCMS'

export const useStoreConfig = defineStore('config', {
  state: () => ({
    baseApi: null,
    baseUrl: null,
    langs: [],
    langSelected: ''
  }),
  actions: {
    loadConfig () {
      try {
        const configElement = document.getElementById('config')
        var config = JSON.parse(configElement.innerHTML)
        this.langs = config.langs
        this.baseUrl = config.baseUrl
        this.baseApi = config.baseApi
        const tempLangSelected = localStorage.getItem(langKey)
        tempLangSelected
          ? (this.langSelected = tempLangSelected)
          : this.setLangSelected(config.langSelected)

        return true
      } catch (error) {
        return error
      }
    },
    setLangSelected (langSelected) {
      localStorage.setItem(langKey, langSelected)
      this.langSelected = langSelected
    }
  }
})
