import { defineStore } from 'pinia'
import { get as getSettings } from '@/api/settings'
import {
  get as getSidebar,
  update as updateSideBar
} from '@/api/settings/sidebar'

import { get as getLogin, update as updateLogin } from '@/api/settings/login'
import { Message } from 'element-ui'

// useStore could be anything like useUser, useCart
// the first argument is a unique id of the store across your application
export const useStoreSettings = defineStore('settings', {
  state: () => ({
    loading: false,
    sideBar: {
      logo: '',
      color: ''
    },
    login: {
      logo: '',
      color: ''
    }
  }),
  actions: {
    async fetchSettings () {
      this.loading = true
      return await getSettings()
        .then(response => {
          // console.log(response.data, 'fetchSettings')
          this.sideBar = response.data.sideBar
          this.login = response.data.login
          return {
            sideBar: this.sideBar,
            login: this.login
          }
        })
        .catch(this.responseCatch)
        .finally(() => (this.loading = false))
    },
    // Sidebar
    async fetchSidebar () {
      this.loading = true
      return await getSidebar()
        .then(response => {
          // console.log(response.data, 'fetchSidebar')
          this.sideBar = response.data
          return this.sideBar
        })
        .catch(this.responseCatch)
        .finally(() => (this.loading = false))
    },
    async updateSidebar () {
      this.loading = true
      return await updateSideBar(this.sideBar)
        .then(response => {
          // console.log(response, 'updateSideBar')
          Message({
            showClose: true,
            message: response.messages[0] || 'Exito!',
            type: 'success'
          })

          return response.data
        })
        .catch(this.responseCatch)
        .finally(() => (this.loading = false))
    },
    // Login
    async fetchLogin () {
      this.loading = true
      return await getLogin()
        .then(response => {
          // console.log(response.data, 'fetchLogin')
          this.login = response.data
          return this.login
        })
        .catch(this.responseCatch)
        .finally(() => (this.loading = false))
    },
    async updateLogin () {
      this.loading = true
      return await updateLogin(this.login)
        .then(response => {
          // console.log(response.data, 'updateLogin')
          Message({
            showClose: true,
            message: response.messages[0] || 'Exito!',
            type: 'success'
          })
          return response.data
        })
        .catch(this.responseCatch)
        .finally(() => (this.loading = false))
    }
  }
})
