import Vue from 'vue'
import { extend, ValidationProvider, ValidationObserver } from 'vee-validate'
import * as rules from 'vee-validate/dist/rules'
import { messages } from 'vee-validate/dist/locale/es.json'
import { validateKeyName as validatePages } from '@/api/pages/index'
import { validateKeyName as validateTemplate } from '@/api/template/index'

import {
  getValidateCollectionTemplate
} from '@/api/collectionTemplate'

// import _ from 'lodash'

Object.keys(rules).forEach(rule => {
  extend(rule, {
    ...rules[rule], // copies rule configuration
    message: messages[rule] // assign message
  })
})
Vue.component('ValidationProvider', ValidationProvider)
Vue.component('ValidationObserver', ValidationObserver)

extend('key', {
  validate: async (value, args) => {
    console.log(args)
    const params = {
      key: value
    }
    if (args._id !== undefined) { params._id = args._id }

    const valid = await getValidateCollectionTemplate(params).then(response => {
      return response.data
    })
    return valid
  },
  message: 'This key Exist',
  params: ['_id']
})

extend('https', {
  validate: value => {
    const regexp = new RegExp(/^(https:\/\/)([\da-z.-]+\.[a-z.]{2,6}|[\d.]+)([/:?=&#]{1}[\da-z.-]+)*[/?]?$/, 'i')

    return regexp.test(value)
  },
  message: 'The {_field_} field must be a valid URL secure'
})

extend('http', {
  validate: value => {
    const regexp = new RegExp(/^(https?:\/\/)?([\da-z.-]+\.[a-z.]{2,6}|[\d.]+)([/:?=&#]{1}[\da-z.-]+)*[/?]?$/, 'i')

    return regexp.test(value)
  },
  message: 'The {_field_} field must be a valid URL'
})

// var messageKeyNAme

extend('uniqueKeyNamePage', {
  validate: async (element) => {
    element = element.replace(/ /g, '_')

    var formKeyName = new FormData()
    formKeyName.append('keyName', element)
    var isValidKeyName

    // const regexp = new RegExp(/^(https?:\/\/)?([\da-z.-]+\.[a-z.]{2,6}|[\d.]+)([/:?=&#]{1}[\da-z.-]+)*[/?]?$/, 'i')
    await validatePages(formKeyName).then((response) => {
      isValidKeyName = response.data.validation
    })

    return !isValidKeyName
  },
  message: 'El keyName ya existe'
})

extend('uniqueKeyNameTemplate', {
  validate: async (element) => {
    element = element.replace(/ /g, '_')

    var formKeyName = new FormData()
    formKeyName.append('keyName', element)
    var isValidKeyName

    // const regexp = new RegExp(/^(https?:\/\/)?([\da-z.-]+\.[a-z.]{2,6}|[\d.]+)([/:?=&#]{1}[\da-z.-]+)*[/?]?$/, 'i')
    await validateTemplate(formKeyName).then((response) => {
      isValidKeyName = response.data.validation
    })

    return !isValidKeyName
  },
  message: 'El keyName ya existe'
})

