import request from '@/utils/request'

export function index (data, type) {
  return request({
    url: '/templates/' + type,
    method: 'post',
    data
  })
}

export function store (data) {
  return request({
    url: '/templates',
    method: 'post',
    data
  })
}

export function show (keyName) {
  return request({
    url: '/templates/' + keyName,
    method: 'get'
  })
}

export function update (keyName, data) {
  return request({
    url: '/templates/' + keyName,
    method: 'put',
    data
  })
}

export function destroy (keyName) {
  return request({
    url: '/templates/' + keyName,
    method: 'delete'
  })
}

export function getPage (type, page, noitems) {
  return request({
    url: '/templates/' + type + '?page=' + page + '&item=' + noitems,
    method: 'post'
  })
}

export function showPage (type, idPage) {
  return request({
    url: '/templates/' + type + '/' + idPage,
    method: 'get'
  })
}

export function updateContent (data, keyName) {
  return request({
    url: '/templates/' + keyName + '/content/editor',
    method: 'put',
    data
  })
}

export function validateKeyName (data) {
  return request({
    url: '/templates/validate/keyname',
    method: 'post',
    data
  })
}
