import Vue from 'vue'
Vue.mixin({
  filters: {
    statusColor (status) {
      const statusMap = {
        true: 'primary',
        false: 'danger'
      }
      return statusMap[status]
    },
    statusFilter (status) {
      const statusMap = {
        true: 'Enabled',
        false: 'Disbled'
      }
      return statusMap[status]
    }
  },
  methods: {
    responseCatch (e) {
      const data = e.response.data
      console.log('responseCatch -> data', data)
      if (data.messages) {
        data.messages.forEach((element) => {
          this.$message.error(element)
        })
      }
    },
    beforeAvatarUpload (file) {
      var acceptFile = true
      const isJPG = file.type === 'image/jpeg'
      const isLt2M = file.size / 1024 < 500

      if (!isJPG) {
        this.$message.error('La imagen debe estar en formato JPG!')
        acceptFile = false
      }
      if (!isLt2M) {
        this.$message.error('La imagen excede los 500kB!')
        acceptFile = false
      }
      return acceptFile
    },
    clearFile (siteStore) {
      siteStore.file.name = ''
      siteStore.file.pathFile = ''
      siteStore.file.type = ''
      siteStore.file.order = 0
    }
  }
})
