import router from './router'
import store from './store'
import { Message } from 'element-ui'
import NProgress from 'nprogress' // progress bar
import 'nprogress/nprogress.css' // progress bar style
import { getToken } from '@/utils/auth' // get token from cookie
import { getPageTitle } from '@/utils'
import { useStoreUser } from './stores/userStore'

NProgress.configure({ showSpinner: false }) // NProgress Configuration

const whiteList = ['/login'] // no redirect whitelist

router.beforeEach(async (to, from, next) => {
  const settings = store.state.settings
  // console.log('TCL: beforeEach -> settings', settings)

  // start progress bar
  NProgress.start()

  // set page title
  document.title = getPageTitle(settings.metaTitle, to.meta.title)

  // determine whether the user has logged in
  const hasToken = getToken()

  if (hasToken) {
    /* console.log(to.fullPath)
    console.log(to.path)
    console.log(location.toString())
    console.log(location.host) */

    if (to.path === '/login') {
      // if is logged in, redirect to the home page
      next({ path: settings.redirectAuth })
      NProgress.done()
    } else {
      // const hasScopes = store.getters.scopes && store.getters.scopes.length > 0

      // REMOVE STORE
      const hasGetUserInfo = store.getters.name
      // REMPLACE STORE
      const userStore = useStoreUser()
      if (hasGetUserInfo) {
        next()
      } else {
        try {
          // REMOVE STORE
          await store.dispatch('user/getInfo')
          // REMPLACE STORE
          userStore.getInfo()

          // await store.dispatch('site/getSite')
          // generate accessible routes map based on roles

          // const accessRoutes = await store.dispatch('permission/generateRoutes', store.getters.scopes)
          // accessRoutes.forEach(function (ruta) {
          //   router.options.routes.push(ruta)
          //   router.addRoute(ruta)
          // })

          // hack method to ensure that addRoutes is complete
          // set the replace: true, so the navigation will not leave a history record
          next({ ...to, replace: true })
          // next()
        } catch (error) {
          // remove token and go to login page to re-login
          Message.error(error || 'Error')

          // REMOVE STORE
          await store.dispatch('user/resetToken')
          // REMPLACE STORE
          userStore.resetToken()

          next(`/login?redirect=${to.path}`)
          NProgress.done()
        }
      }
    }
  } else {
    /* has no token */

    if (whiteList.indexOf(to.path) !== -1) {
      // in the free login whitelist, go directly
      next()
    } else {
      // other pages that do not have permission to access are redirected to the login page.
      next(`/login?redirect=${to.path}`)
      NProgress.done()
    }
  }
})

router.afterEach(() => {
  // finish progress bar
  NProgress.done()
})
