import request from '@/utils/request'

export function store (data) {
  return request({
    url: '/images',
    method: 'post',
    data
  })
}

export function storeBase64 (data) {
  return request({
    url: '/images/base-64',
    method: 'post',
    data
  })
}

export function search (data) {
  return request({
    url: '/images/search',
    method: 'post',
    data
  })
}

export function getPage (page, noitems) {
  return request({
    url: '/images/search?page=' + page + '&item=' + noitems,
    method: 'post'
  })
}

export function destroy (id) {
  return request({
    url: '/images/' + id,
    method: 'delete'
  })
}
